.Cart {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--primary-color);
    border-radius: 10px;
    width: 300px;
    padding: 10px;
    height: 450px;
    box-shadow: 0 0 10px 1px black;
    transform: scale(1);
    transition: transform .2s ease, box-shadow .2s ease;
}

.Cart:hover {
    transform: scale(1.02);
    box-shadow: 0 0 15px 2px black;
}
