.CarouselStore {
    display: flex;
    width: 100%;
    justify-content: center;
    gap: 100px;
    align-items: center;
    flex-wrap: wrap;

    > div:first-child {
        width: 600px;
        height: 400px;
    }

    >div:last-child {
        width: 800px;
    }
}

.carousel {
    width: 50vw;
    height: 600px;
}
