.Header {
    position: sticky;
    width: 100vw;
    display: flex;
    justify-content: space-around;
    height: 125px;
    align-items: center;
    background-color: var(--header-color);
    border-radius: 0 0 50px 50px;
    top: 0;
    z-index: 4;
    font-family: Foglihten;
    text-shadow: 0 0 1px black;
    font-size: var(--font-size-xl);
    color: var(--primary-color);
}

.logo {
    width: 220px;
    text-align: center;
}
