.Modal {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    opacity: 0;
    pointer-events: none;
    z-index: -1;
}

.overlay {
    width: 100%;
    height: 100%;
    background: var(--overlay-color);
    display: flex;
    align-items: center;
    justify-content: center;
}

.content {
    padding: 20px;
    width: 1000px;
    border-radius: 12px;
    background: var(--primary-color);
    transition: .3s transform;
    transform: scale(.5);
    max-width: 60%;
}

.opened {
    pointer-events: auto;
    opacity: 1;
    z-index: 5;

    .content {
        transform: scale(1);
    }
}

.isClosing {
    .content {
        transform: scale(.2);
    }
}
