@font-face {
    font-family: Foglihten;
    src: url("../../../shared/fonts/FoglihtenNo06_076.otf");
}

@font-face {
    font-family: Gilroy;
    src: url("../../../shared/fonts/Gilroy-Light.otf");
}

:root {
    --font-family-main: "Gilroy", "Times New Roman", serif;
    --font-family-logo: "Foglihten";
    --font-size-xl: 45px;
    --font-xl: var(--font-size-xl) var(--font-family-main);
    --font-size-l: 32px;
    --font-l: var(--font-size-l) var(--font-family-main);
    --font-size-m: 24px;
    --font-m: var(--font-size-m) var(--font-family-main);
    --font-size-s: 20px;
    --font-s: var(--font-size-s) var(--font-family-main);
    --bg-color: #bcb8b1;
    --logo-color: #7a1833;
    --header-color: #ab4e6873;
    --primary-color: #ab4e68;
    --secondary-color: #000;
    --overlay-color: #00000073;
}
