.Image {
    position: relative;
    display: flex;
    width: 100%;
    height: 100%;
    flex-grow: 0;
}

.image {
    position: absolute;
    margin: auto;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    object-fit: cover;
    object-position: bottom;
}

.contain {
    .image {
        position: relative;
        object-fit: contain;
        align-self: center;
    }
}
