.Carousel {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.wrapper {
    display: flex;
    width: 100%;
    position: relative;
}

.contentWrapper {
    overflow: hidden;
    width: 100%;
    height: 100%;
}

.content {
    display: flex;
    transition: transform .5s ease-out;
    -ms-overflow-style: none;  /* hide scrollbar in IE and Edge */
    scrollbar-width: none;  /* hide scrollbar in Firefox */
    > * {
        width: 100%;
        flex-shrink: 0;
        flex-grow: 1;
    }
}

/* hide scrollbar in webkit browser */
.content::-webkit-scrollbar {
    display: none;
}

.arrowLeft,
.arrowRight {
    position: absolute;
    z-index: 1;
    top: 50%;
    width: 48px;
    height: 48px;
    border-radius: 24px;
    border: 0;
    background-color: var(--primary-color);
    transform: scale(1);
    transition: transform .2s ease;
}

.arrowLeft {
    left: 24px;
}

.arrowRight {
    right: 24px;
}

.arrowLeft:hover,
.arrowRight:hover {
    transform: scale(1.25);
}
