.Text {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.center {
    align-items: center;
    text-align: center;
    gap: 10px;
}

.title {
    font: var(--font-xl);
    color: var(--primary-color);
}

.miniTitle {
    font: var(--font-l);
}

.text {
    font: var(--font-m);
    white-space: pre-wrap;
}

.miniText {
    font: var(--font-s);
}

.price {
    font: var(--font-l);
    align-self: flex-end;
}
